import React from "react";
import DeleteIcon from "@material-ui/icons/Delete";


function Note(props) {
  function handleClick() {
    props.onDelete(props.id);
  }

  const playlistID = ((props.iframe).substring((props.iframe).indexOf('playlist/'), (props.iframe).indexOf('?'))).substr(9, );
  const frameSRC = "https://open.spotify.com/embed/playlist/"+ playlistID ;

  return (
    <div className="note">
       

      <span className="genreDisplay">{props.genre}</span>
      <span className="moodDisplay">{props.mood}</span>

      <iframe  className="frame" src={frameSRC} width="220" height="290" frameborder="0" color="blue" allowtransparency="true" allow="encrypted-media"></iframe>
      
      {/* <button onClick={handleClick}>
        <DeleteIcon />
      </button> */}
    </div>
  );
  
}



export default Note;
