import React, { Component } from "react";

class Radio extends Component {
  constructor() {
    super();
    this.state = {
      name: "React"
    };
    this.onGenreChange = this.onGenreChange.bind(this);
    this.onMoodChange = this.onMoodChange.bind(this);
    this.formSubmit = this.formSubmit.bind(this);
    this.filterSubmit = this.filterSubmit.bind(this);
    this.resetFilter= this.resetFilter.bind(this);

  }


  onGenreChange(event) {
    this.setState({
      selectedGenre: event.target.value
    });
  }
  onMoodChange(event) {
    this.setState({
      selectedMood: event.target.value
    });
  }

  formSubmit(event) {
    event.preventDefault();
  }

 filterSubmit(event) {
  {this.props.onFilter(this.state.selectedMood,this.state.selectedGenre)}
  };
    
  resetFilter(event){
    var ele = document.getElementsByName("radio-group-2");
    for(var i=0;i<ele.length;i++)
       ele[i].checked = false;
    var elem = document.getElementsByName("radio-group");
    for(var i=0;i<elem.length;i++)
       elem[i].checked = false;

    this.setState({
      selectedMood: undefined
    });
    this.setState({
        selectedGenre: undefined
      });

    {this.props.onReset()}
  };

  render() {
  return (
    <form onSubmit={this.formSubmit}>
    <div className="radio-pillbox">
  
  <radiogroup className="radio">
    <div>
    <label className="test1"  for="Indie" className="radio-label">
    <input 
              onChange={this.onGenreChange} type="radio" name="radio-group" id="js" value="Indie" className="first"/>
    </label>
    <p>Indie</p>
    </div>

    <div>
    <label for="Rap">
      <input onChange={this.onGenreChange} type="radio" name="radio-group" id="html5" value="Rap"/>
      </label> 
      <p>Rap</p> 
    </div>
    
    <div>
    <label   for="css"> 
    <input onChange={this.onGenreChange} type="radio" name="radio-group" id="css" value="Rock"/>
    </label>
    <p>Rock</p>
    </div>

    <div>
    <label for="angularjs">
    <input onChange={this.onGenreChange} type="radio" name="radio-group" id="angularjs" value="Country"/>
    </label>
    <p>Country</p>
      
    </div>
    <div>
    <label for="jquery">
    <input onChange={this.onGenreChange} type="radio" name="radio-group" id="jquery" value="Jazz"/>
    </label>
    <p>Jazz</p>
      
    </div>
    <div>
    <label for="rn">
    <input onChange={this.onGenreChange} type="radio" name="radio-group" id="rn" value="Electronic" className="last"/>
     </label>
     <p>Electronic</p>
      
      
    </div>
  </radiogroup>

</div>

<div className="radio-moodbox">
  
  <radiogroup>
    <div>
    <label   for="Study" className="radio-label">
    <input 
              onChange={this.onMoodChange} type="radio" name="radio-group-2" id="study" value="Study" className="first"/>
    </label>
    <p>Study</p>
    </div>

    <div>
    <label for="Relax">
      <input onChange={this.onMoodChange} type="radio" name="radio-group-2" id="relax" value="Relax"/>
      </label> 
      <p>Relax</p> 
    </div>
    
    <div>
    <label   for="Party"> 
    <input onChange={this.onMoodChange} type="radio" name="radio-group-2" id="party" value="Party"/>
    </label>
    <p>Party</p>
    </div>

    <div>
    <label for="Workout">
    <input onChange={this.onMoodChange} type="radio" name="radio-group-2" id="workout" value="Workout"/>
    </label>
    <p>Workout</p>
      
    </div>
    <div>
    <label for="Dining">
    <input onChange={this.onMoodChange} type="radio" name="radio-group-2" id="dining" value="Dining" />
    </label>
    <p>Dining</p>
      
    </div>
    <div>
    <label for="New-Releases">
    <input onChange={this.onMoodChange} type="radio" name="radio-group-2" id="new-releases" value="New Releases" className="last"/>
     </label>
     <p>Fresh Finds</p>
      
      
    </div>
  </radiogroup>

</div>


<div >
          <p className="genreShifter" ><span class="customBadge badge badge-light">genre: </span>  <span class=" customBadge2 badge badge-pill badge-success">{this.state.selectedGenre}</span></p>
          
          <p  className="moodShifter" > <span class="customBadge badge badge-light">mood:</span> <span class="customBadge2 badge badge-info">{this.state.selectedMood}</span> </p>
          
        
          <button  className="resetButtonShifter  btn-lg btn btn-outline-secondary" onClick={this.resetFilter}>
        
        Reset Filter
    </button>

        <button  className="submitButtonShifter btn-lg btn btn-info" onClick={this.filterSubmit}  type="submit">
          Find Playlists!
        </button>
        
        </div>
</form>


    
  );
}
}

export default Radio;