import React, { useEffect, useState } from "react";
import { FirebaseDatabaseProvider } from "@react-firebase/database";

import Header from "./Header";
import Footer from "./Footer";
import Note from "./Note";
import CreateArea from "./CreateArea";
import Radio from "./Radio";
import playlists from "../playlists.js";
import firebase from "../util/firebase";



function App() {

  
  
  const [notes, setNotes] = useState([]);

  
  useEffect(() => {
    const playlistRef = firebase.database().ref("playlistCard");
    playlistRef.on("value", (snapshot)=>{
      //console.log(snapshot.val());
      const playlists = snapshot.val();
      const playlistArray = [];
      for (let id in playlists ){
        playlistArray.push(playlists[id]);
      }
      //console.log(playlistArray);
      //see if the line below slows the code down

      playlistArray.reverse();
      setNotes(playlistArray);
      

    });
  

  },[])

  function addNote(newNote) {
    
    
    setNotes(prevNotes => {
      return [newNote, ...prevNotes ];
    });
  
  };

  function resetPlaylists() {
    const playlistRef = firebase.database().ref("playlistCard");
    playlistRef.on("value", (snapshot)=>{
      //console.log(snapshot.val());
      const playlists = snapshot.val();
      const playlistArray = [];
      for (let id in playlists ){
        playlistArray.push(playlists[id]);
      }
      //console.log(playlistArray);
      setNotes(playlistArray);
      //console.log(notes);

    });

  };

  

  function filterPlaylist(mood,genre) {
    setNotes(prevNotes => {
      if (mood === undefined && genre === undefined){
        return prevNotes}
      if (mood == undefined){
      return prevNotes.filter(playlist => playlist.note.genre === genre);}
       if (genre == undefined){
        return prevNotes.filter(playlist => playlist.note.mood === mood);}
        
        else{
          if (mood != undefined && genre != undefined){
            return prevNotes.filter(playlist => playlist.note.mood === mood && playlist.note.genre === genre)};
          
        }
    });

    

    //console.log( typeof mood,  typeof genre);
  }

  function deleteNote(id) {
    setNotes(prevNotes => {
      return prevNotes.filter((noteItem, index) => {
        return index !== id;
      });
    });
  }

  return (
    
    <div>
      <Header />
      <Radio 
        onFilter={filterPlaylist}
        onReset={resetPlaylists}
      />
      
      <CreateArea onAdd={addNote} />

      {notes.map((noteItem, index) => {
        return (
          <Note
            key={index}
            id={index}
            genre={noteItem.note.genre}
            mood={noteItem.note.mood}
            content={noteItem.note.content}
            iframe={noteItem.note.iframe}
            onDelete={deleteNote}
          />
        );
      })}
      

      {/* {playlists.filter(playlist => playlist.mood === 'Study').map(playlistItem => (
        <Note
            genre={playlistItem.genre}
            mood={playlistItem.mood}
            content={playlistItem.content}
            iframe={playlistItem.iframe}
            onDelete={deleteNote}
          />
  ))} */}

      {/* {playlists.map((playlistItem, index) => {
        return (
          <Note
            key={index}
            id={index}
            genre={playlistItem.genre}
            mood={playlistItem.mood}
            content={playlistItem.content}
            iframe={playlistItem.iframe}
            onDelete={deleteNote}
          />
        );
      })} */}
      
      
    </div>
    

  );
  
}

export default App;
