import firebase from "../util/firebase";
import $ from "jquery";
import React, { useState } from "react";
import AddIcon from "@material-ui/icons/Add";
import Fab from "@material-ui/core/Fab";
import Zoom from "@material-ui/core/Zoom";
import Select from 'react-select';
import CloseOutlinedIcon from '@material-ui/icons/CloseOutlined';
var validator = require('validator');
 

function CreateArea(props) {

const genreMenu = [
  { value: 'genre', label: 'Indie' },
  { value: 'genre', label: 'Rap' },
  { value: 'genre', label: 'Rock' },
  { value: 'genre', label: 'Country' } ,
  { value: 'genre', label: 'Jazz' },
  { value: 'genre', label: 'Electronic' } 
];
const moodMenu = [
    { value: 'mood', label: 'Study' },
    { value: 'mood', label: 'Relax' },
    { value: 'mood', label: 'Party' },
    { value: 'mood', label: 'Workout' } ,
    { value: 'mood', label: 'Dining' },
    { value: 'mood', label: 'Fresh Finds' } 
  ];

  const [isExpanded, setExpanded] = useState(false);

  const [note, setNote] = useState({
    genre: "",
    mood:"",
    iframe: ""
  });



  function handleChange(event) {
    const { name, value } = event.target;
   

    setNote(prevNote => {
      return {
        ...prevNote,
        [name]: value
      };
    });
    
  }


  function handleChange2(event) {
  
    const { value, label } = event;
   
    setNote(prevNote => {
      return {
        ...prevNote,
        [value]: label
      };
    });
    // console.log(note);
  }

  const createPlaylist = () =>{
    const playlistCardRef  = firebase.database().ref("playlistCard");
    
    const playlistinfo ={
        note,
        complete:false,

    };
    playlistCardRef.push(playlistinfo);
    setNote({
        genre: "",
        mood: "",
        iframe: ""
      });}

  function submitNote(event) {
    props.onAdd(note);
    setNote({
      genre: "",
      mood: "",
      iframe: ""
    });
    event.preventDefault();
  }

  function expand() {
    setExpanded(true);
  }
  function close() {
    setExpanded(false);
  }

  return (
    <div>
      <form className="create-note">
        {isExpanded && (

            <Select name="genre"
            onChange={handleChange2}
             placeholder={"Genre"} options = {genreMenu} />
          /* <input
            name="genre"
            onChange={handleChange}
            value={note.genre}
            placeholder="Genre"
          /> */
          
        )}
        {isExpanded && (
          <Select name="mood"
            onChange={handleChange2}
             placeholder={"Mood"} options = {moodMenu} />
          
        )}

 

        <textarea
        id="lightText"
          name="iframe"
          onClick={expand}
          onChange={handleChange}
          value={note.iframe}
          placeholder={isExpanded ? "paste a Spotify 'Playlist Link' here. For example: https://open.spotify.com/playlist/37i9dQZ..." : "Add your own playlist... "}
          rows={isExpanded ? 3 : 1}
        />
        <Zoom in={isExpanded}>
        
        
   
          <Fab onClick={validator.contains(note.iframe, "open.spotify") ?
        createPlaylist : () => 
        {setNote(prevNote => {
      return {
        ...prevNote,
        iframe: ""
      };
    }); 
        $('#lightText').attr('placeholder','Looks like that link is not valid, please try again!'); }} >
            <AddIcon />
          </Fab>
        </Zoom>
        
        <div  >
        <Zoom in={isExpanded}>
        
        <Fab id="createNote2" onClick={close}>
          <CloseOutlinedIcon />
        </Fab>
      </Zoom>
        </div>
        
      </form>
      {/* <div>
      <input type="text" />
      <button onClick={createPlaylist}>add to do</button>
      </div> */}
    </div>
  );
}

export default CreateArea;